.App {
  display: flex;
  align-items: center;
  justify-content: center;

  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;

  background-color: black;
  overflow: hidden;
}

.SurfaceContainer {
  width: 100%;
  overflow: hidden;
  
}

canvas {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  overflow: hidden;
}

.MenuClosed {
  position: absolute;
  left: 20px;
  top: 20px;
  width: 100px;
  height: 100px;

}

.MenuOpen {
  position: absolute;
  left: 0;
  top: 0;
  width: 300px;
  height: 600px;
  background-color: #AAAAAA22;
  padding: 20px;
  border-radius: 0 0 20px 0;
  border: 1px solid black;
}

button {
  padding: 10px;
  font-size: large;
  background-color: #DDCCAA44;
  border: none;
  font-family: inherit;
}

input {
  font-size: large;
  border: 1px solid #333311AA;
  font-family: inherit;
  background-color: #DDCCAA77;
  padding: 10px;

}

input[type="color"] {
  padding: 0px;
}
